import React, { Component } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";

class Igfeed extends Component {

    componentDidMount() {

        //These functions add the required IG widget scripts
        const script = document.createElement("script");
    
        script.src = "https://snapwidget.com/js/snapwidget.js";
        script.async = true;
    
        document.body.appendChild(script);
    
        const script2 = document.createElement("script");
    
        script2.src = "https://snapwidget.com/js/snapwidget-lightbox.js";
        script2.async = true;
    
        document.body.appendChild(script2);
      }

  render() {
    return (
        <section id="igFeed" className="d-flex align-items-center mb-0">
        <Container className="p-5">
          <Row className="justify-content-center">
            <iframe
              src="https://snapwidget.com/embed/671108"
              title="IG widget"
              className="snapwidget-widget"
              allowtransparency="true"
              frameBorder="0"
              scrolling="no"
              style={{ border: "none", overflow: "hidden", width: "100%" }}
            />
          </Row>
        </Container>
      </section>
    );
  }
}

export default Igfeed;